.work-cell-row {
  margin-top: 2vw;

  .name {
    margin-left: 2vw;

  }
}

.main-body-work {
  padding: 0 5vw;
  .ant-switch-checked {
    background-color: #78fe78;
  }
  .quote-left{
    //font-size: 30px;
    //color: #f2f2f2;
  }
  .work-title {
    font-size: large;
  }
  .work-money {
    h1 {
      display: inline-block;
      color: #eecb94;
      font-weight: bold;
    }
    .work-times{
      display: inline-block;
      float: right;
    }
  }
  .more-info{
    padding: 3vw;
    background-color: #f2f2f2;
    .quote-right{
      float: right;
    }
    .blue-bold-font{
      color: #2d8cdd;
      font-weight: bold;
    }

  }
  .my-workers {
    margin-top: 2vh;
    //border-top: 1px lightgrey solid;
    font-size: small;
  }

  .work-cell-row {
    margin-top: 3vw;
  }

  .work-url {
    border-radius: 10px;
    text-align: center;
    width: 40vw;
    height: 30px;
    line-height: 30px;
    background-color: #f8626a;
    color: white;
  }
}
.owner{
  margin-bottom: 10vh;
}
.work-submit-footer {
  position: fixed;
  //padding: 2vw;
  bottom: 0vh;
  right: 0;
  left: 0;
  z-index: 100;
  background-color: white;
  font-weight: 700;
  font-size: 1.3em;
  //color: white;
  //display: flex;
  //flex-direction: column;
  //justify-content: center;
  //align-items: center;
  text-align: center;

  .submit-footer-cell {
    //height: 6vh;
    //line-height: 6vh;
    font-size: 1em;
    width: 100vw;
    //background-color: #71ceff;
    div{
      display: inline-block;
      margin: 0;
    }
    .share{
      color: #199bca;
      text-align: center;
      font-weight: bold;
      padding: 2vw;
      //border-right: 1px solid gray;
    }
    .action{
      .communications{
        color: #199bca;
        text-align: center;
        font-weight: bold;
        padding: 2vw;
        .font-small{
          font-size: 10px;
        }
      }
        .upload-work{
          color: white;
          background-color: #fa325f;
        }
        .working{
          background-color: #88c9eb;
        }
    }

    //border-radius: 40px;
    .orange {
      display: inline-block;
      height: 100%;
      background-color: #ff9469;
    }
  }

  .failed {
    background-color: gray;
  }
}
